/* eslint-disable */
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button, Navbar, Container, Nav, Row, Col } from 'react-bootstrap';
// import bg from './img/bg.png';
import { useState } from "react";
import { data } from './data.js';
import { Routes, Route, Link, useNavigate, Outlet } from 'react-router-dom';
import { Main, Goods, Detail, About, Event } from './page.js';
import { Cart } from './routes/Cart.js';

import styled from 'styled-components';
let YellowBtn = styled.button`
 background:${props => props.color};
 color:black;
 padding:10px;
 border:none;
`

function App() {

  let [shoes, setShoes] = useState(data);
  let navigate = useNavigate();
  let [moreCount, setMoreCount] = useState(0);

  return (
    <div className="App">


      {/* <YellowBtn color="yellow">버튼</YellowBtn> */}

      <Navbar bg="light" variant="light">
        <Container>
          <Navbar.Brand>ShoeShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/cart') }}>Cart</Nav.Link>
            <Nav.Link onClick={() => { navigate('/about') }}>About</Nav.Link>
          </Nav>
        </Container>
      </Navbar>


      <Routes>
        <Route path="*" element={<div>잘못된 주소입니다</div>} />
        <Route path="/" element={<Main shoes={shoes} setShoes={setShoes} moreCount={moreCount} setMoreCount={setMoreCount} />} />
        <Route path="/detail/:id" element={<Detail shoes={shoes} />} />
        <Route path="/about" element={<About />} >
          <Route path="member" element={<div>멤버</div>} />
          <Route path="location" element={<div>위치정보</div>} />
        </Route>
        <Route path="/cart" element={<Cart />} />
      </Routes>

    </div>
  );
}

export default App;