import bg from './img/bg.png';
import { data } from './data.js';
import { useEffect, useState } from "react";
import { Outlet, useParams, Link } from 'react-router-dom';
import { Button, Nav } from 'react-bootstrap';
import axios from 'axios'
import { useDispatch, useSelector } from "react-redux";
import { addCart } from "./store.js";

function Main({ shoes, setShoes, moreCount, setMoreCount }) {

    let [isAscending, setIsAscending] = useState(false);
    let [noMore, setNoMore] = useState(false);
    let [load, setLoad] = useState(false)

    return (
        <>
            <div className="main-bg" style={{ backgroundImage: 'url(' + bg + ')' }}></div>
            <div className='container'>
                <div className='row'>
                    <Goods shoes={shoes} />
                </div>
                {
                    noMore == true ?
                        <div>
                            <h4>상품이 없습니다</h4>
                        </div>
                        : null
                }

                {
                    load == true ?
                        <div>
                            <h4>로딩중입니다</h4>
                        </div>
                        : null
                }
            </div>
            <Button variant="warning" onClick={() => {
                let sortedShoes = [...shoes].sort(function (a, b) {
                    let x = a.title.toLowerCase();
                    let y = b.title.toLowerCase();
                    if (isAscending == false) {
                        setIsAscending(true)
                        if (x < y) { return -1; }
                        if (y < x) { return 1; }
                        return 0;
                    } else {
                        setIsAscending(false)
                        if (x > y) { return -1; }
                        if (y > x) { return 1; }
                        return 0;
                    }
                })
                setShoes(sortedShoes)
            }}>정렬</Button>

            <button onClick={() => {
                setLoad(true)
                if (moreCount < 2) {
                    axios.get(`https://codingapple1.github.io/shop/data${moreCount + 2}.json`)
                        .then((result) => {
                            setLoad(false)
                            setShoes(prevShoes => [...prevShoes, ...result.data])
                        })
                        .catch(() => {
                            setLoad(false)
                            console.log('실패')
                        })
                    setMoreCount(moreCount + 1)
                } else {
                    setLoad(false)
                    setNoMore(true)
                }

            }}>더보기</button>

            <button onClick={() => {
                console.log(shoes)
            }}>test</button>

        </>
    )
}

function Goods({ shoes }) {
    return (
        <>
            {
                shoes.map(function (item, i) {
                    let imgUrl = `https://codingapple1.github.io/shop/shoes${item.id + 1}.jpg`;
                    return (
                        <div className='col-md-4' key={i}>
                            <Link to={`/detail/${item.id}`}><img src={imgUrl} width='80%' /></Link>
                            {/* <a href={`/detail/${item.id}`}><img src={imgUrl} width='80%' /></a> */}
                            <h4>{item.title}</h4>
                            <p>{item.content}</p>
                            <p>{item.price}</p>
                        </div>
                    )
                })
            }
        </>
    )
}

function Detail(props) {

    let { id } = useParams();
    let shoes = props.shoes.find((a) => a.id == id)
    let imgUrl = `https://codingapple1.github.io/shop/shoes${parseInt(id) + 1}.jpg`;
    let [alert, setAlert] = useState(true);
    let [inputAlert, setInputAlert] = useState(false);
    let [tab, setTab] = useState(0);

    let dispatch = useDispatch()
    let cart = useSelector((state) => state.cart);


    let [fade, setFade] = useState('');
    useEffect(() => {
        setFade('end');
        return () => {
            setFade('');
        }
    }, [])

    useEffect(() => {
        let timer = setTimeout(() => {
            setAlert(false)
        }, 2000)
        return () => {
            clearTimeout(timer);
        }
    })

    let [num, setNum] = useState('');
    useEffect(() => {
        if (num == '') {
            setInputAlert(false)
        } else if (isNaN(num)) {
            setInputAlert(true)
        } else {
            setInputAlert(false)
        }
    }, [num])


    return (
        <div className={`start ${fade}`}>
            <div className="container">
                {
                    alert == true ?
                        <div className='alert alert-warning'>
                            2초이내 구매시 할인
                        </div>
                        : null
                }

                <div className="row mt-2">
                    <div className="col-md-6">
                        <img src={imgUrl} width="100%" />
                    </div>

                    <div className="col-md-6">
                        {
                            inputAlert == true ?
                                <div className='alert alert-danger'>*** 숫자만 쓰시라고 ***</div>
                                : null
                        }
                        <input type='text' placeholder='숫자만 입력하세요' onChange={(e) => { setNum(e.target.value) }}></input>

                        <h4 className='pt-3'>{shoes.title}</h4>
                        <p>{shoes.content}</p>
                        <p>{shoes.price}원</p>
                        <button className="btn btn-danger" onClick={() => {
                            dispatch(addCart([shoes.id, shoes.title]))
                        }}>주문하기</button>
                    </div>
                </div>
            </div>

            <Nav variant="tabs" defaultActiveKey="link0">
                <Nav.Item>
                    <Nav.Link eventKey="link0" onClick={() => setTab(0)}>버튼0</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link1" onClick={() => setTab(1)}>버튼1</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link2" onClick={() => setTab(2)}>버튼2</Nav.Link>
                </Nav.Item>
            </Nav>
            <Tab tab={tab} />
        </div>
    )
}

function Tab({ tab }) {
    let [fade, setFade] = useState('');

    useEffect(() => {
        let timer = setTimeout(() => {
            setFade('end');
        }, 100)
        return () => {
            clearTimeout(timer);
            setFade('');
        }
    }, [tab])
    return (<div className={`start ${fade}`}>
        {[<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][tab]}
    </div>)
}


function About() {
    return (
        <div>
            <h4>정보</h4>
            <Outlet></Outlet>
        </div>
    )
}

function Event() {
    return (
        <div>
            <h3>오늘의 이벤트</h3>
            <Outlet></Outlet>
        </div>
    )
}

export { Main, Goods, Detail, About, Event };