import { Table } from 'react-bootstrap';
import { useDispatch, useSelector } from "react-redux";
import { changeName, increaseAge, increaseCount, decreaseCount } from "./../store.js";

function Cart() {

    let item = useSelector((state) => state.cart)
    let user = useSelector((state) => state.user)
    let dispatch = useDispatch()

    return (
        <div>
            <h5>{user.name}의 장바구니 {user.age}</h5>
            <button onClick={() => dispatch(increaseAge(3))}>age++</button>
            <button onClick={() => dispatch(changeName())}>name</button>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>변경하기</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        item.map((a, i) => {
                            return (
                                <tr key={i}>
                                    <td>{i + 1}</td>
                                    <td>{item[i].name}</td>
                                    <td>{item[i].count}</td>
                                    <td>
                                        <button onClick={()=>{
                                            dispatch(decreaseCount(item[i].id))
                                        }}>-</button>
                                        <button onClick={() => {
                                            dispatch(increaseCount(item[i].id))
                                        }}>+</button>
                                    </td>

                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        </div>
    )
}

export { Cart };