import { configureStore, createSlice } from '@reduxjs/toolkit'

let user = createSlice({
    name: 'user',
    initialState: {name : 'kim', age : 20},
    reducers: {
        changeName() {
            return { name: 'park', age: 20 }
        },
        increaseAge(state, action) {
            state.age += action.payload;
        }
    }
})

export let { changeName, increaseAge } = user.actions;

let cart = createSlice({
    name: 'cart',
    initialState: [
        { id: 0, name: 'White and Black', count: 1 },
        { id: 2, name: 'Grey Yordan', count: 1 }
    ],
    reducers: {
        increaseCount(state, action) {
            let i = state.findIndex((e) => e.id === action.payload);
            state[i].count ++;
        },
        decreaseCount(state, action){
            let i = state.findIndex((e) => e.id === action.payload);
            if(state[i].count!=0){
                state[i].count --;
            }
        },
        addCart(state, action) {
            let id = action.payload[0];
            let name = action.payload[1];
            let i = state.findIndex((item) => item.id === id);
            if(i==-1){
                state.push({id:id, name:name, count:1});
            }else{
                state[i].count++;
            }
            
        }
    }
})

export let { increaseCount, decreaseCount, addCart } = cart.actions;


export default configureStore({
    reducer: {
        user: user.reducer,
        cart: cart.reducer
    }
}) 